<template>
  <div>
    <!--面包屑导航-->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>健康码管理</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/personalcode' }">健康码详情</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <!--搜索头部input框-->
      <el-row :gutter="15">
        <el-col :span="8">
          <el-input v-model="queryInfo" class="input-with-select" clearable placeholder="请输入内容"
                    @clear="getCodeList">
            <el-button slot="append" icon="el-icon-search" @click="searchCodeByName"></el-button>
          </el-input>
        </el-col>
        <el-col :span="5">
          <el-button type="primary" @click="addVisible=true">添加数据</el-button>
          <el-button type="danger" @click="batchDelete(tableChecked)">批量删除</el-button>
        </el-col>
      </el-row>
      <el-table
          :cell-style="rowClass"
          :data="CodeList"
          :header-cell-style="headClass"
          :row-style="rowStyle"
          border
          style="width: 100%"
          @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column
            label="序号"
            prop="id"
            width="45px">
        </el-table-column>
        <el-table-column
            label="姓名"
            prop="username"
            width="80px">
        </el-table-column>
        <el-table-column
            label="电话"
            prop="mobile"
            width="110">
        </el-table-column>
        <el-table-column
            label="住址"
            prop="address"
            width="100px">
        </el-table-column>
        <el-table-column label="健康码">
          <template slot-scope="scope">
            <el-image :preview-src-list="[scope.row.code_url]" :src="scope.row.code_url"
                      style="width: 30px;height: 30px">
              <div slot="error" class="image-slot"></div>
              <i class="el-icon-picture-outline"></i>
            </el-image>
          </template>
        </el-table-column>
        <el-table-column
            label="上传时间"
            prop="date">
        </el-table-column>
        <el-table-column>
          <template slot-scope="scope">
            <el-radio v-model="scope.row.status" label="未审核" @change="choose(scope.row)">未审核</el-radio>
            <el-radio v-model="scope.row.status" label="已审核" @change="choose(scope.row)">已审核</el-radio>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="100px">
          <template slot-scope="scope">
            <el-tooltip class="item" content="删除" effect="dark" placement="top-start">
              <el-button circle icon="el-icon-delete" size="mini" type="danger"
                         @click="deleteCode(scope.row)"></el-button>
            </el-tooltip>
            <el-tooltip class="item" content="转为隔离" effect="dark" placement="top-start">
              <el-button circle icon="el-icon-plus" size="mini" type="primary"
                         @click="addDivide(scope.row)"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--分页功能-->
      <el-pagination
          :current-page="pageNum"
          :page-size="pageSize"
          :page-sizes="[5,8,10,12,15]"
          :total="total"
          layout="total, sizes, prev, pager, next, jumper"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange">
      </el-pagination>
    </el-card>

    <!--增加Info对话框-->
    <el-dialog :visible.sync="addVisible" title="增加数据" width="35%" @close="closeAddDialog">
      <!--这是内容区域-->
      <el-form ref="addFormRef" :model="Code" :rules="mobileFormRules" class="demo-Info" label-width="100px">
        <el-form-item label="电话" prop="mobile">
          <el-input v-model="Code.mobile"></el-input>
        </el-form-item>
        <el-form-item label="住址" prop="address">
          <el-input v-model="Code.address"></el-input>
        </el-form-item>
        <el-form-item label="选择日期时间" prop="date">
          <el-date-picker
              style="float: left"
              v-model="Code.date"
              placeholder="选择日期时间"
              size="large"
              type="datetime"
              value-format="yyyy-MM-dd HH:mm:ss">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="上传健康码">
          <el-upload :auto-upload="false" :limit="1" :on-change="uploadListChange" action="" list-type="picture-card"
                     style="float: left">
            <i class="el-icon-plus"></i>
          </el-upload>
        </el-form-item>
      </el-form>
      <el-button @click="addVisible = false">取 消</el-button>
      <el-button type="primary" @click="addCode">确 定</el-button>
    </el-dialog>

    <!--添加dialog对话框-->
    <el-dialog
        :visible.sync="addDivideVisible"
        title="添加数据"
        width="35%"
        @close="closeAddDivideDialog"
    >
      <!--这是内容区域-->
      <el-form ref="addFormRef" :model="Divide" :rules="mobileFormRules" class="demo-user" label-width="100px">
        <el-form-item label="姓名" prop="username">
          <el-input v-model="Divide.username"></el-input>
        </el-form-item>
        <el-form-item label="电话：" prop="mobile">
          <el-input v-model="Divide.mobile"></el-input>
        </el-form-item>
        <el-form-item label="隔离地点：" prop="area">
          <el-input v-model="Divide.area"></el-input>
        </el-form-item>
        <el-form-item label="体温：" prop="tw">
          <el-input v-model="Divide.tw" style="width: 100px;float:left"></el-input>
          <div style="float:left">°C</div>
        </el-form-item>
        <el-form-item label="症状1：" style="float:left">
          <el-checkbox v-model="Divide.status1" prop="status1" true-label="咳嗽：">咳嗽</el-checkbox>
        </el-form-item>
        <el-form-item label="症状2：" style="float:left">
          <el-checkbox v-model="Divide.status2" prop="status2" true-label="感冒">感冒</el-checkbox>
        </el-form-item>
        <el-form-item label="症状3：" style="float:left">
          <el-checkbox v-model="Divide.status3" prop="status3" style="float:left" true-label="发烧">发烧</el-checkbox>
        </el-form-item>
        <el-form-item label="备注：" prop="bz">
          <el-input v-model="Divide.bz"></el-input>
        </el-form-item>
        <el-form-item label="隔离日期：" prop="createTime">
          <el-date-picker
              v-model="Divide.createTime"
              placeholder="选择日期时间"
              size="large"
              style="float: left"
              type="datetime"
              value-format="yyyy-MM-dd HH:mm:ss">
          </el-date-picker>
        </el-form-item>
      </el-form>
      <el-button @click="addDivideVisible = false">取 消</el-button>
      <el-button type="primary" @click="ConfirmAddDivide">确 定</el-button>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "PersonalCode",
  data() {
    //验证手机号码的规则
    const checkMobile = (rule, value, cb) => {
      const regMobile = /^1[345789]\d{9}$/
      if (regMobile.test(value)) {
        return cb()
      }
      //返回一个错误提示
      cb(new Error('请输入合法的手机号码'))
    };
    return {
      pageNum: 1,
      pageSize: 5,
      total: null,
      queryInfo: '',
      addVisible: false,
      tableChecked: [],
      CodeList: [],
      Code: {},
      id: null,
      Divide: {},
      addDivideVisible: false,
      codeFormData: '',
      status: '',
      mobileFormRules: {
        mobile: [
          {validator: checkMobile, trigger: 'blur'}
        ],
      }
    }
  },
  methods: {
    //自动传入
    handleSelectionChange(val) {
      console.log(val)
      this.tableChecked = val;
    },
    headClass() {  //表头居中显示
      return "text-align:center"
    },
    rowClass() { //表格数据居中显示
      return "text-align:center"
    },
    rowStyle({row}) {
      if (row.status === "未审核") {
        return {
          backgroundColor: '#dddcd6',
        }
      }
    },
    handleSizeChange(newSize) {
      this.pageSize = newSize;
      this.getCodeList();
    },
    handleCurrentChange(newNum) {
      this.pageNum = newNum;
      this.getCodeList();
    },
    getCodeList() {
      this.$axios.get('/admin/code/getAllCodeList', {
        params: {
          pageNum: this.pageNum,
          pageSize: this.pageSize
        }
      }).then(res => {
        this.CodeList = res.data.data.list;
        this.total = res.data.data.total;
      })
    },
    searchCodeByName() {
      this.$axios.post('/code/search?userName=' + this.queryInfo).then(res => {
        console.log(res)
        this.CodeList = res.data.data;
        this.total = res.data.data.length;
      })
    },
    choose(row) {
      this.$axios.post('/code/updateStatus', row).then(res => {
        if (res.data.status === 10000) {
          this.$message.success("更新成功！")
        } else {
          this.$message.error("更新失败！")
        }
      })
    },
    closeAddDialog() {
      this.$refs.addFormRef.resetFields();
    },
    uploadListChange: function (file, fileList) {
      let formData = new FormData;
      fileList.forEach(item => {  //fileList本来就是数组，就不用转为真数组了
        formData.append("file", item.raw);  // 将每一个文件图片都加进formData
      })
      this.codeFormData = formData;
    },
    addCode() {
      this.$refs.addFormRef.validate(valid => {
        if (valid) {
          this.$axios.post('/code/oosFile/upload', this.codeFormData).then(res => {
            console.log(res, '上传图片的结果')
            if (res.data.code === 200) {
              this.Code.codeUrl = res.data.data
              console.log(this.Code)
              this.$axios.post('/code/add', this.Code).then(res => {
                console.log(res, '添加到数据库之后的结果')
                if (res.data.status === 10000) {
                  this.$message.success("添加成功！")
                  this.addVisible = false;
                } else {
                  this.$message.error("添加失败！")
                }
              })
            } else {
              this.$message.error("添加失败！")
            }
          })
        } else {
          alert("请输入正确的信息")
        }
      })
    },
    editCode(row) {
      console.log(row)
    },
    closeEditDialog() {
      //重置表单
      this.$refs.addFormRef.resetFields();
    },
    deleteCode(row) {
      this.$confirm('此操作将永久删除该商品, 是否继续?', '删除商品', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$axios.get('/code/delete?id=' + row.id).then(res => {
          console.log(res)
          if (res.data.status === 10000) {
            this.$message.success("删除成功！")
            this.getGoodsList();
          } else {
            this.$message.error("删除失败")
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    batchDelete(ids) {
      console.log(ids)
    },
    addDivide(row) {
      this.addDivideVisible = true;
      this.Divide.username = row.username;
      this.Divide.mobile = row.mobile;
      this.id = row.id;
    },
    ConfirmAddDivide() {
      this.Divide.tw = this.Divide.tw + "°C";
      this.$axios.post('/divide/add', this.Divide).then(res => {
        if (res.data.status === 10000) {
          this.$axios.get('/code/delete?id=' + this.id).then(res => {
            if (res.data.status === 10000) {
              this.$message.success("转移成功！")
              this.getCodeList();
            } else {
              this.$message.error("转移失败！删除出错")
              this.getCodeList();
            }
          })
        } else {
          this.$message.error("转移失败！添加到隔离人员失败！")
          this.getCodeList();
        }
        this.addDivideVisible = false;
      })
    },
    closeAddDivideDialog() {
      this.$refs.addFormRef.resetFields();
    }
  },
  created() {
    this.getCodeList()
  }
}
</script>

<style scoped>

</style>
